            className="relative w-full max-w-md animate-[bounce_0.5s_ease-out] rounded-3xl bg-gradient-to-br from-christmas-sand via-white to-christmas-sand p-8 shadow-2xl shadow-christmas-sand/60 ring-4 ring-christmas-berry"
